import { Component, OnInit } from '@angular/core';
import { HttpClient}  from "@angular/common/http";
import { Observable } from "rxjs";
import {articleserver, article } from './article-list.module';
import {webconfig} from '../config/config';
import { Router } from '@angular/router';
import {articleServe} from '../article-detail/article.serve'
@Component({
  selector: 'article-list',
  templateUrl: './article-list.component.html',
  styleUrls: ['./article-list.component.css']
})
export class articlesComponent implements OnInit {

  articleserver: Observable<articleserver>;  
  articles: article[];
  show_manu:boolean;
  message:string;
  constructor(private articleServeh: articleServe,private httpClient:HttpClient,private router: Router,) {
    
  }
  ngOnInit(): void {
    this.httpClient
    .get<articleserver>(webconfig.backendurl+"./article",{
    responseType: 'json'})
    .subscribe(data => {
      this.articles=data.articles;
    },
    err => {
      console.log("Error occured.",err)
    });
    this.show_manu= (localStorage.getItem("editmode")==="open")
  }
  checkDetails(id:string):void {
    this.router.navigate(['/article', id]);
  }
  createNewArticle(){
    this.router.navigate(['/article', "create"]);
  }
  editArticle(id:string,e){
    e.stopPropagation();
  }

  deleteArticle(id:string,e){
    e.stopPropagation();
    this.articleServeh.deleteArticle(id).subscribe(
      data =>{
        if(data.success){
          this.message="success delete";
        }
        else{
          this.message="error delete";
        }
      }
    )
  }
}

