import { HttpClient,HttpHeaders }  from "@angular/common/http";
import {webconfig,backendurl} from '../config/config';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { catchError} from 'rxjs/operators';
import { HttpErrorHandler, HandleError } from '../httpserve/http-error-handler.service';
import {article } from '../article-list/article-list.module';
const httpOptions = {
    headers: new HttpHeaders({
      'Content-Type':  'application/json'
    },
    )
  };

@Injectable({
    providedIn: 'root',
  })
export class articleServe {
    URL:any;  // URL to web api
    private handleError: HandleError;
    constructor(private httpClient: HttpClient,httpErrorHandler: HttpErrorHandler){
        this.handleError = httpErrorHandler.createHandleError('articleService');
        this.URL=backendurl.articles

    }
    
    deleteArticle(articleId:string):Observable<any>{
        return this.httpClient
         .delete<any>(`${webconfig.backendurl}${this.URL.base}/${articleId}`,
         httpOptions )
        .pipe(
           catchError(this.handleError('delete atricle',false))
            )
    }
    updateArticle(article:article):Observable<any>{
      return this.httpClient
      .put<any>(`${webconfig.backendurl}${this.URL.base}/${article._id}`,article,
      httpOptions )
     .pipe(
        catchError(this.handleError('update atricle',false))
         )
    }
    createArticle(article:article):Observable<any>{
      return this.httpClient
      .post<any>(`${webconfig.backendurl}${this.URL.base}/${this.URL.create}`,article,
      httpOptions )
     .pipe(
        catchError(this.handleError('update atricle',false))
         )

    }
    getArticle(id:String):Observable<any>{
      return this.httpClient
      .get<any>(`${webconfig.backendurl}${this.URL.base}/${id}`,
      httpOptions )
     .pipe(
        catchError(this.handleError('update atricle',false))
         )
    }

}