import { Component, OnInit } from '@angular/core';
import {article } from '../article-list/article-list.module';
import {webconfig} from '../config/config';
import { HttpClient}  from "@angular/common/http";
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import {articleServe} from './article.serve'
import { GlobalService } from '../common/services/global.service';
@Component({
  selector: 'app-article-detail',
  templateUrl: './article-detail.component.html',
  styleUrls: ['./article-detail.component.css'],
  providers: [ GlobalService ]
})
export class ArticleDetailComponent implements OnInit {
  article: article;
  message:string;
  title:string;
  show_manu:boolean;
  constructor(private articleServeh: articleServe,private router: Router,private httpClient:HttpClient,public globals: GlobalService) {
   }
  createMode:boolean;
  ngOnInit() {

    var id = this.router.url.substring(this.router.url.lastIndexOf('/')+1)
    // this.httpClient
    // .get<any>(url,{
    // responseType: 'json'
    // })
    // .subscribe(data => {
    //   this.article=data.article;
    //   this.article.content=JSON.parse(this.article.content)
    // },
    // err => {
    //   console.log("Error occured.",err)
    // });
    this.createMode=(id==="create")
    if (!this.createMode){
      this.getArticle(this.router.url.substring(this.router.url.lastIndexOf('/')+1))
    }
    else{
      this.iniArticle()
    }
    this.show_manu= (localStorage.getItem("editmode")==="open")


  }
  getArticle(id:String):void{
    this.articleServeh.getArticle(id).subscribe(data => {
      this.article=data.article;
      this.article.content=this.article.content
    },
    err => {
      console.log("Error occured.",err)
    });
  }
  iniArticle():void{ 
    this.article = {} as article;
    this.article.title="new article";
    this.article.content="";
  }
  createnarticle():void{
    console.log(this.article)
    this.articleServeh.createArticle(this.article).subscribe(
      data=>{
        this.message=data.success
      }
    )
  }
  deleteArticle():void{
    this.globals.globalBannerType="success"
    this.globals.globalBannerType="HHHHHH it is impossible"
  }
  saveArticle():void{
    if(this.createMode){
      this.createnarticle()
    }
    else{
      this.updateArticle()
    }
  }
  updateArticle():void{
    this.articleServeh.updateArticle(this.article).subscribe(
      data=>{
        this.message=data.success

      }
    )
  }


}
